import { useState } from "react"
import { DropDown } from "./dropdown/DropDown"
import { ResearchSimulation } from "./researchsimulation/ResearchSimulation"
import { SelectFromRange } from "./selectfromrange/SelectFromRange"
import { Slider } from "./slider/Slider"
import { TextArea } from "./textarea/TextArea"
import { TextInput } from "./textinput/TextInput"
import { replaceBBCode } from "./../../../helpers/bbcode"
import { SelfAssessment } from "./selfassessment/SelfAssessment"

// @TODO find better solution for generating unique element ids
let renderedElementId = 0



export const FormBuilder = ({ formSections, formName, onFormChanged, onFormComplete, defaultValues }) => {
  const [completedInputFields, setCompletedInputFields] = useState({})

  /**
   * Renders a section headline
   */
  const renderHeadline = data => {
    return (
      <div className="text-xl pt-3 pb-4" key={data.text}>
        <strong>{data.text}</strong>
      </div>
    )
  }

  /**
   * Renders a bunch of text paragraphs
   */
  const renderText = data => {
    return data.paragraphs.map((p, idx) => (
      <p className="pb-3" key={idx} dangerouslySetInnerHTML={{
        __html: replaceBBCode(p)
      }}></p>
    ))
  }

  const renderBoxedText = data => {
    const text = renderText(data)
    return <div className="border border-gray-400 bg-gray-200 p-5 border-solid">
      {text}
    </div>
  }

  const renderBulletList = data => {
    const bPoints = data.paragraphs.map((p, idx) => {
      return <li className="pb-3 list-item" key={idx}>{p}</li>
    })

    return <ul className="pl-10 list-outside list-disc">
      {bPoints}
    </ul>
  }

  const getRequiredFields = (secs) => {
    return secs.reduce((acc, section) => {
      const requestedFieldsForSection = section.elements?.reduce((acc, el) => {
        if (el.type?.toLowerCase() === 'input' && el.data.isOptional !== true) {
          acc.push(el.data.name)
        }

        /*if (el.module === 'pickversion') {
          el.data.versions.forEach(ver => {
            acc.push(getRequiredFields(ver))
          })
          acc.flat()
        }*/

        return acc
      }, [])

      if (requestedFieldsForSection) {
        acc.push(requestedFieldsForSection)
      }

      return acc
    }, []).flat()
  }

  const formIsCompleted = () => {
    if (!completedInputFields) {
      return false
    }

    if (completedInputFields && Object.keys(completedInputFields).length < 1) {
      return true
    }

    let isCompleted = true

    const listOfRequiredFields = getRequiredFields(formSections)
    listOfRequiredFields.forEach(reqFieldName => {
      if (completedInputFields[reqFieldName] === undefined || completedInputFields[reqFieldName] === null) {
        isCompleted = false
      }
    })

    return isCompleted
  }

  const stashInput = (xname, xvalue) => {
    const ci = completedInputFields
    ci[xname] = xvalue
    setCompletedInputFields(ci)
  }

  const saveCurrentInputValue = e => {
    stashInput(e.target.name, e.target.value)
    onFormChanged(formName, completedInputFields)

    if (formIsCompleted()) {
      onFormComplete(true)
    } else {
      onFormComplete(false)
    }
  }


  const renderSlider = (data) => {
    const { label, name, unit, min, max, ...otherAttributes } = data

    return (
      <div key={data.name} className="bg-gray-200 p-5 w-full">
        <Slider
          name={name}
          className="min-w-full"
          label={label}
          unit={unit}
          onChange={saveCurrentInputValue}
          min={min}
          max={max}
        />
      </div>
    )
  }


  const renderSelectFromRange = (data) => {
    return (
      <div key={data.name} className="bg-gray-200 p-5">
        <SelectFromRange
          name={data.name}
          label={data.label}
          options={data.options}
          onChange={saveCurrentInputValue}
        />
      </div>
    )
  }


  /**
   * Renders a multiline text input field (<textarea...)
   */
  const renderTextArea = ({ name, label, rows, placeholder }) => {
    const curVal = completedInputFields?.[name] || null
    return (
      <div key={name} className="border-1 border-solid bg-gray-200 p-5">
        <TextArea
          className="w-full p-5"
          onBlur={saveCurrentInputValue}
          onChange={() => null}
          onComplete={saveCurrentInputValue}
          label={label}
          name={name}
          rows={rows}
          placeholder={placeholder}
          defaultValue={curVal}
        />
      </div>
    )
  }

  /**
   * Renders a single line (<input ..) or a multiline (<textarea.. ) input field
   */
  const renderTextInput = ({ name, label, rows, placeholder }) => {
    if (rows > 1) {
      return renderTextArea({ name, label, rows, placeholder })
    }

    const curVal = completedInputFields?.[name] || null
    return (
      <div key={name} className="border-1 border-solid bg-gray-200 p-5">
        <TextInput
          className="w-full p-5"
          onBlur={saveCurrentInputValue}
          onChange={() => null}
          onComplete={saveCurrentInputValue}
          label={label}
          name={name}
          rows={rows}
          placeholder={placeholder}
          defaultValue={curVal}
        />
      </div>
    )
  }

  const renderDropDown = (data) => {
    return (
      <div key={data.name} className="border-1 border-solid bg-gray-200 p-5">
        <DropDown
          name={data.name}
          label={data.label}
          options={data.options}
          onValueSelect={saveCurrentInputValue}
        />
      </div>
    )
  }

  const renderResearchSimulation = (data) => {
    return (
      <div key={data.name} className="border-1 border-solid bg-gray-200 p-5">
        <ResearchSimulation
          name={data.name}
          onSearchHistoryChanged={saveCurrentInputValue}
        />
      </div>
    )
  }

  const renderLinkThumbnail = (data) => {
    return (
      <a href={data.url} target={data.target}>
        <div key={data.name} className="border-1 border-solid bg-gray-100 p-5 w-auto hover:bg-gray-200">
          {data.title}
        </div>
      </a>
    )
  }

  const renderPickVersion = (data) => {
    const numVersions = data.versions?.length
    if (numVersions <= 0) return null

    const choosenVersion = Math.floor(Math.random() * numVersions)

    return data.versions[choosenVersion].elements?.map((el, idx) => {
      if (!el.module) return null

      if (el.module !== 'subsections') {
        return <div className="__element p-2" key={el.id}>{renderIncludedElement(el)}</div>
      }

      return null
    })
  }

  const renderAside = (elements) => {
    const sideElements = elements.map((el, idx) => {
      if (!el.module) return null

      if (el.module !== 'subsections') {
        return <div className="__element p-2" key={el.id}>{renderIncludedElement(el)}</div>
      }

      return null
    })

    return (<div className="absolute right-0 top-0 h-screen">
      <div className="__element p-2 sticky right-0 top-0">
        {sideElements}
      </div>
    </div>)
  }



  const renderSelfAssessment = (data) => {
    return (
      <div key={data.name} className="border-1 border-solid bg-gray-200 p-5">
        <SelfAssessment
          data={data}
          onAllChosen={saveCurrentInputValue}
        />
      </div>
    )
  }

  /**
   * Renders an element inside a cell or section
   */
  const renderIncludedElement = (el) => {
    let content = null
    el.data.id = renderedElementId++

    switch (el.module.toLowerCase()) {
      case 'aside':
        content = renderAside(el.elements)
        break
      case 'selfassessment':
        content = renderSelfAssessment(el.data)
        break
      case 'linkthumbnail':
        content = renderLinkThumbnail(el.data)
        break
      case 'researchsimulation':
        content = renderResearchSimulation(el.data)
        break
      case 'dropdown':
        content = renderDropDown(el.data)
        break
      case 'selectfromrange':
        content = renderSelectFromRange(el.data)
        break
      case 'slider':
        content = renderSlider(el.data, el.settings)
        break
      case 'headline':
        content = renderHeadline(el.data)
        break
      case 'boxedtext':
        content = renderBoxedText(el.data)
        break
      case 'bulletlist':
        content = renderBulletList(el.data)
        break
      case 'pickversion':
        content = renderPickVersion(el.data)
        break
      case 'text':
        content = renderText(el.data)
        break
      case 'textarea':
      case 'textinput':
        content = renderTextInput(el.data)
        break
      default:
        break
    }

    return content
  }

  /**
   * Renders a collection of sections (including all nested subsections)
   */
  const renderSections = (secs, depth) => {
    let content = null

    return secs.map((sec, secIdx) => {
      const elements = sec.elements.map((el, idx) => {
        if (!el.module) return null

        if (el.module !== 'subsections') {
          content = renderIncludedElement(el)

          return <div className="__element p-2" key={`${el.id}`}>{content}</div>
        } else {
          content = renderSections(el.sections, depth + 1)

          return content
        }
      })

      if (depth === 1) {
        return (
          <div className="p-6 border my-1 border-solid" key={`sect-${depth}:${secIdx}`}>
            {elements}
          </div>
        )
      } else {
        return (
          <div className="__subsection" key={`subsect-${depth}:${secIdx}`}>
            {elements}
          </div>
        )
      }
    })
  }

  const formContent = renderSections(formSections, 1)

  return <>
    {formContent}
  </>
}