import { useEffect, useState } from 'react'
import clsx from 'clsx'
import { FormBuilder } from './formbuilder/FormBuilder'
import { SimpleModal } from './simplemodal/SimpleModal'

const getPage = (pageSet, pageId) => {
  const pages = pageSet.filter(val => val.thisId === pageId)
  if (pages.length > 1) { console.error(`Found more than 1 page with id (${pageId})`) }
  return pages[0]
}

const getFirstPage = (pageSet) => {
  const rootPages = pageSet.filter(val => val.prevId === null)
  if (rootPages.length > 1) { console.error('Found more than 1 root page') }
  return rootPages[0]
}


export const SurveyManager = ({ ruleSet, onSaveData, sessionId }) => {
  const pages = ruleSet?.pages

  const [currentPage, setCurrentPage] = useState(null)
  const [currentPageIsComplete, setCurrentPageIsComplete] = useState(false)
  const [surveyIsComplete, setSurveyIsComplete] = useState(false)
  const [surveyData, setSurveyData] = useState({ sessionId: sessionId })
  const [modalContent, setModalContent] = useState({ show: false })

  useEffect(() => {
    if (currentPage?.settings?.allowProceed === true) {
      setCurrentPageIsComplete(true)
    }
  })

  if (!pages || pages.length < 1) {
    console.error('No pages defined in survey')
    return null
  }

  if (!currentPage) {
    const firstPage = getFirstPage(pages)
    if (firstPage && firstPage.thisId >= 0) {
      setCurrentPage(firstPage)
    }
    return null
  }

  const saveSurveyData = () => {
    surveyData.surveyId = ruleSet.meta.surveyId
    onSaveData(surveyData)
  }

  const triggerNextPage = (e) => {
    if (currentPage.nextId) {
      if (currentPage.settings?.confirmMoveForward) {
        const { title, text, buttons } = currentPage.settings?.confirmMoveForward

        setModalContent({
          show: true,
          title,
          text,
          buttons,
          onAccept: () => { setModalContent({ show: false }); setCurrentPageIsComplete(false); setCurrentPage(getPage(pages, currentPage.nextId)) },
          onReject: () => { setModalContent({ show: false }) }
        })
      } else {
        setCurrentPageIsComplete(false)
        setCurrentPage(getPage(pages, currentPage.nextId))
      }
    } else {
      saveSurveyData()
      setSurveyIsComplete(true)
    }
  }

  const triggerPrevPage = (e) => {
    if (currentPage.prevId) {
      setCurrentPage(getPage(pages, currentPage.prevId))
    }
  }

  const handleFormChanged = (formName, formData) => {
    const fName = formName || "general"
    let sData = surveyData
    sData[fName] = formData
    setSurveyData(sData)
    console.log('Survey Data:', sData)
  }

  const handleFormComplete = (isCompleted) => {
    setCurrentPageIsComplete(isCompleted)
  }

  let formContent = null

  if (surveyIsComplete) {
    formContent = (<div className='flex h-screen'>
      <div className='text-black font-bold text-3xl m-auto text-4xl'>Umfrage beendet. Sie dürfen das Fenster jetzt schließen.</div>
    </div>)
  } else {
    formContent = (currentPage.sections?.length > 0) ?
      <FormBuilder
        key={`form_${currentPage.thisId}`}
        defaultValues={surveyData[currentPage]}
        formSections={currentPage.sections}
        formName={`page:${currentPage.thisId.toString().padStart(2, '0')}`}
        onFormChanged={handleFormChanged}
        onFormComplete={handleFormComplete}
      /> :
      <div>no form data</div>
  }

  const renderBackButton = () => {
    if (currentPage.settings?.allowMoveBack !== true) {
      return null
    }

    return (<button className={clsx({
      "m-5 bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline": currentPageIsComplete === true
    })} type="button" onClick={triggerPrevPage}>
      Zurück
    </button>)
  }

  const renderNextButton = () => {
    if (surveyIsComplete) return null

    if (currentPage.nextId === null) {
      currentPage.labelNextPage = 'Umfrage beenden'
    }

    return (<button disabled={currentPageIsComplete === false && 'disabled'} className={clsx({
      "m-5 bg-blue-300 text-white font-bold py-2 px-4 rounded opacity-50 cursor-not-allowed": currentPageIsComplete === false,
      "m-5 bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline": currentPageIsComplete === true
    })} type="button" onClick={triggerNextPage}>
      {currentPage.labelNextPage || 'Weiter'}
    </button>)
  }

  const backButton = renderBackButton()
  const nextButton = renderNextButton()

  const {
    show,
    title,
    text,
    buttons,
    onAccept,
    onReject
  } = modalContent

  const modalWindow = show ?
    <SimpleModal
      title={title}
      text={text}
      buttons={buttons}
      onAccept={onAccept}
      onReject={onReject}
    />
    : null


  return (
    <div key={currentPage.thisId}>
      <div className="flex justify-end">
        {!surveyIsComplete && <div className="text-white bg-gray-400 px-2 py-0">
          Seite: {currentPage.thisId}
        </div>}
      </div>
      {formContent}
      <div className="flex items-center justify-between">
        {backButton}
        {nextButton}
      </div>
      {modalWindow}
    </div>
  )
}